.form {
  width: 340px;
  /* height: 440px; */
  /* background: #e6e6e6; */
  /* border-radius: 8px; */
  /* box-shadow: 0 0 40px -10px #000; */
  margin: calc(30vh - 220px) auto;
  padding: 20px 10px;
  max-width: calc(100vw - 40px);
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  position: relative;
}

input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: none;
  outline: none;
  resize: none;
  border: 0;
  font-family: "Montserrat", sans-serif;
  transition: all 0.3s;
  border-bottom: 2px solid #bebed2;
}
input:focus {
  border-bottom: 2px solid #78788c;
}
p:before {
  content: attr(type);
  display: block;
  margin: 28px 0 0;
  font-size: 14px;
  color: #5a5a5a;
}
.send {
  float: center;
  padding: 8px 12px;
  margin: 20px;
  font-family: "Montserrat", sans-serif;
  border: 2px solid #78788c;
  background: 0;
  color: white;
  background-color: black;
  cursor: pointer;
  transition: all 0.3s;
}
.send:hover {
  background: rgb(108, 218, 255);
  color: #fff;
}

span {
  margin: 0 5px 0 15px;
}

.contactForm {
  padding-bottom: 50px;
}

textarea {
  width: 100%;
  padding: 10px;
  margin-top: 20px;
